import {
  ApolloClient, ApolloLink, HttpLink, InMemoryCache,
} from '@apollo/client'
import { onError } from '@apollo/link-error'
import fetch from 'isomorphic-unfetch'
import { resolvers, typeDefs } from './schema'

export const port = process.env.NODE_ENV === 'production' ? 8080 : 4000
export const isBrowser = typeof localStorage !== 'undefined'

// let data;
let token

if (isBrowser) {
  token = localStorage.getItem('token')
  // const data = {
  //   isAuthenticated: !!token,
  //   projectId: localStorage.getItem("projectId"),
  //   versionId: localStorage.getItem("versionId"),
  //   statusId: localStorage.getItem("statusId"),
  //   teamId: localStorage.getItem("teamId"),
  // }
  // cache.writeData({ data })
}

export default (initialState, ctx) => new ApolloClient({
  ssrMode: Boolean(ctx),
  link: ApolloLink.from([
    onError(({ graphQLErrors, networkError }) => {
      if (graphQLErrors) {
        graphQLErrors.forEach(({ message, locations, path }) => console.log(
          `[GraphQL error]: Message: ${message}, Location: ${locations}, Path: ${path}`,
        ))
      }
      if (networkError) {
        console.log(
          `[Network error]: ${networkError}. Backend is unreachable. Is it running?`,
        )
      }
    }),
    new HttpLink({
      uri: `http://localhost:${port}/graphql`,
      credentials: 'include',
      headers: {
        authorization: token || null,
      },
      fetch,
    }),
  ]),
  cache: new InMemoryCache({}).restore(initialState || {}),
  resolvers,
  typeDefs,
})
